<template>
    <div>
        <div class="addbox">
            <transition name="move">
                <div class="del" v-show="food.count>0" @click.stop.prevent="del($event)">-</div>
            </transition>
            
            <div class="num" v-show="food.count>0">{{food.count}}</div>
            <div class="add" @click.stop.prevent="add($event)" ref="add">+</div>
        </div>
        
    </div>
</template>

<script type="text/javascript">
    import Vue from 'vue';
    export default{
        props:{
            food:{
                type:Object
            }
        },
        data:function(){
            return {
                
            }
        },
        mounted:function(){
            
        },
        methods:{
            add:function(ev){
                var el=ev.target;
                var elt=el.getBoundingClientRect().top;
                var ell=el.getBoundingClientRect().left;
        
                if(!ev._constructed){  //better-scroll自定义派发的事件，原生浏览器是没有的
                    return;
                }

                if(!this.food.count){
                    
                    Vue.set(this.food,'count',1);
                }else{
                    this.food.count++;
                };

                this.$emit('cartAdd',el);//向父组件发送cartadd事件，父组件监听该事件，并触发listen事件
            },
            del:function(ev){
                if(!ev._constructed){  //better-scroll自定义派发的事件，原生浏览器是没有的，防止pc端多次点击
                    return;
                }
                this.food.count--;
                if(this.food.count<=0){
                    this.food.count=0;

                }

            }
        }
    }
</script>

<style lang="scss" scoped>
    .addbox{
        font-size:0;
        position:relative;height:20px;
        &>div{display:inline-block;}
        .del,.add{width:12px;height:12px;background:#57BFFA;border-radius:50%;text-align:center;line-height:10px;font-size:12px;color:#fff;padding:2px;position:absolute;}
        .num{left:0.17rem;position:absolute;top:2px;width:12px;height:12px;text-align:center;}
        .del{margin-right:2px;left:0px;
            
        }
        .add{margin-left:2px;right:6px;}
    }
    .move-enter-active {
        transition: all 0.3s ease;
    }
    .move-leave-active {
        transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    .move-enter, .move-leave-active {
        transform:translateX(30px) rotate(300deg);
        opacity: 0;
    }

</style>


