<template>
    <div id="header" style="position:relative;">
        
        <div class="wraper" >
            <div class="avtar">
                <img :src="seller.avatar" alt="" />
            </div>
            <div class="cont">
                <div class="title">
                    <span class="brand"></span>
                    <span class="name">{{seller.name}}</span>
                </div>
                <div class="desc">
                    {{seller.description}}/{{seller.deliveryTime}}分钟送达
                </div>
                <div v-if="seller.supports" class="support">
                    <span class="icon"></span>
                    <span class="text" >{{seller.supports[2].description}}</span> 
                </div>
            </div>
            <div v-if="seller.supports" class="supportcont">
                {{seller.supports.length}}>
            </div>
        </div>
        <div class="notic" @click="show">
            <span class="too">公告</span>
            <span class="text">{{seller.bulletin}}</span>
            <span class="item">&gt;</span>
        </div>
        <div class="background">
            <img :src="seller.avatar" alt="" />
        </div>
        <transition enter-active-class="bounceInLeft"
        leave-active-class="bounceOutRight">
        <div class="mask animated bounceInLeft" v-show="showdetail" transition="yincang" >
            <div class="detailwraper">
                <div class="detail">
                    <h2>{{seller.name}}</h2>
                    <div class="starwrap">
                        <star :size="48" :score="seller.score"></star>
                    </div>
                    

                </div>
            </div>
            <div class="close" @click="close">&times;</div>
        </div>
        </transition>
    </div>
</template>
<script >
    import Star from './star/star.vue';
    export default{

        props:{
            seller:{
                type:Object
            } 
        },
        data:function(){
            return {
                showdetail:false
            }
        },
        methods:{
            show:function(){
                this.showdetail=true;
            },
            close:function(){
                 this.showdetail=false;
            }
        },
        components:{
            star:Star
        },
        transitions:{
            yincang:{
                enterClass:'bounceInLeft',
                leaveClass:'bounceOutRight'
            }
        }
        
    }

</script>
<style lang="scss" scoped>
    
    @import "../common/main.scss";
    
    .wraper{
        @include clearfloat;
        position:relative;
        padding:0.1rem;
        background:rgba(0,0,0,0.3);
        &>div{float:left;}
        .avtar{
            width:0.64rem;
            height:0.64rem;
            img{
                width:100%;height:100%;
                border-radius:2px;
            }
        };
        .cont{
            margin-left:0.1rem;
            .title{
                color:#fff;
                height:0.2rem;
                line-height:0.2rem;
                span{
                    display:inline-block;
                    font-size:16px;
                    font-weight:bold;
                }
            };
            .desc{height:0.2rem;
                color:#fff;margin-top:5px;
            };
            .support{
                height:0.2rem;
                color:#fff;
            }
        }
        .supportcont{
            color:#fff;
            position:absolute;
            bottom:0.1rem;right:0.1rem;
        }
    }
    .notic{
        width:100%;
        height:0.3rem;
        line-height:0.3rem;
        background:rgba(0,0,0,0.4);
        @include clearfloat;
        &>span{float:left;}
        .too{width:0.3rem;height:100%;color:#fff;background:#999;text-align:center;};
        .text{
            width:calc( 100% - 0.55rem);
            color:#fff;padding-left:0.05rem;
            white-space:nowrap;
            overflow:hidden;
            text-overflow:ellipsis;  
        };
        .item{width:0.2rem;height:100%;color:#fff;
            text-align:center;
        }
    }
    .background{position:absolute;top:0;left:0;width:100%;
        height:100%;z-index:-1;filter:blur(10px);
        img{width:100%;height:100%;}
    }
    .mask{width:100%;height:100%;
            position:fixed;top:0;left:0;
            background:rgba(7,17,27,0.7);
            overflow:auto;
            z-index:100;
            -webkit-backdrop-filter:blur(10px);//仅对ios有用
        .detailwraper{
            min-height:100%;
            .detail{
                padding-bottom:60px;
                padding-top:20px;
                h2{font-size:16px;color:#fff;text-align:center;};
                .starwrap{text-align:center;margin-top:18px;}
            }
        };
        .close{
            position:relative;
            width:100%;
            height:60px;
            line-height:60px;
            font-size:40px;
            color:#fff;
            text-align:center;
            margin-top:-60px;
            clear:both;
           
        }
    }
   
</style>


