<template>
    <div>
        <div class="cartbox">
            <div class="cont">
                <div class="left">
                    <div class="img" @click="toggle">
                        <div class="child" :class="{high:totalcount>0}"><i class="icono-cart"></i></div>
                        <div class="num" v-show="totalcount>0">{{totalcount}}</div>
                    </div>
                    <div class="price">
                        ￥{{totalprice}}
                    </div>
                    <div class="notic" >
                        另需{{deliveryprice}}元配送费
                    </div>
                </div>
                <div class="right" :class="{pay:totalprice>=minPrice}" @click="pay">
                    {{paydes}}
                </div>
            </div>
             <transition enter-active-class="slideInUp"
        leave-active-class="slideOutDown">
            <div class="seletbox animated" v-show="listshow" >
                <div class="head">
                    <span>购物车</span>
                    <span @click="empty">清空</span>
                </div>
                <div class="conts" ref="conts">
                    <ul>
                        <li class="list" v-for="food in sellectfoods">
                            <span class="listname">{{food.name}}</span>
                            <span class="listprice">￥{{food.price*food.count}}</span>
                            <!-- <add></add> -->
                            <div class="addvue">
                                <add :food="food"></add>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            
            </transition>
            <transition enter-active-class="fadeIn" leave-active-class="fadeOut">
                <div class="listmask animated" v-show="listshow"></div>
            </transition>
        </div>
    </div>
</template>

<script type="text/javascript">
    import Bscroll from 'better-scroll';
    import add from '../add.vue';
    export default{
        props:{
            sellectfoods:{
                type:Array,
                default:[
                    {price:10,count:0}
                ]
            },
            deliveryprice:{
                type:Number,default:0
            },
            minPrice:{
                type:Number,default:0
            }
        },
        data:function(){
            return {               
                open:false
            }
        },
        computed:{
            totalprice:function(){
                var tpr=0;
                this.sellectfoods.forEach(function(val){
                    tpr+=val.price*val.count;
                });
                return tpr;
            },
            totalcount:function(){
                var count=0;
                this.sellectfoods.forEach(function(val){
                    count+=val.count;
                });
                return count;
            },
            paydes:function(){
                if(this.totalprice>=this.minPrice){
                    
                    return '去结算';
                }else if(this.totalprice==0){
                    return '￥'+this.minPrice+'起送'
                }else if(this.totalprice<this.minPrice){
                    var dis=this.minPrice-this.totalprice;
                    return '还差￥'+dis+'元起送';
                }
            },
            listshow:function(){
                if(!this.totalcount){
                    this.open=false;
                    return false;
                }
                var show=this.open;
                var _this=this;
                if(show){
                    this.$nextTick( function () { //dom更新加载完再执行scroll函数   
                        if(!_this.conts) {
                            _this.conts=new Bscroll(_this.$refs.conts,{
                                click:true
                            });
                        }else{   //如果已经有实例化的话，直接执行refresh方法
                            _this.conts.refresh();
                        }         
                        
                      
                    });
                }
                return show;
            }
        },
        mounted:function(){
            
            
        },
        methods:{
            toggle:function(){
                if(this.totalcount){
                    this.open=!this.open;                  
                }
                
            },
            empty:function(){
                this.sellectfoods.forEach(function(food){
                    food.count=0;
                });
            },
            pay:function(){
                alert('需要您支付'+this.totalprice+'元');
            },
            childfn:function(el){
                console.log(el);
            }

        },
        components:{
           add:add
        }
    }
</script>

<style lang="scss" scoped>

    .cartbox{
        width:100%;
        position:fixed;
        height:0.46rem;
        bottom:0;
        left:0;z-index:2000;
        .cont{
            display:flex;
            .left{
                flex:1;height:0.46rem;
                background:#141C27;
                .img{
                    position:relative;background:#141C27;border-radius:50%;float:left;
                    width:0.48rem;height:0.48rem;top:-0.05rem;left:0.05rem;
                    //
                    .child{
                        position:absolute;
                        width:0.36rem;
                        height:0.36rem;background:#2B343B;margin-top:50%;margin-left:50%;
                        top:-0.18rem;left:-0.18rem;border-radius:50%;
                        &.high{background:#0087DA;}
                        .icono-cart{height:20%;width:50%}
                    }
                    .num{width:0.24rem;height:0.16rem;background:#f60;border-radius:10px;position:absolute;top:-2px;right:0px;text-align:center;line-height:0.15rem;color:#fff;}
                };
                .price{
                    float:left;color:#fff;padding:0 0.1rem 0 0.1rem;height:0.3rem;line-height:0.3rem;margin-top:0.09rem;border-right:1px solid #333;
                };
                .notic{float:left;height:0.46rem;line-height:0.46rem;color:#767A81;padding-left:0.1rem;}
            };
            .right{
                width:1.05rem;background:#2B343B;
                flex:0 0 1.05rem;height:0.46rem;color:#80858a;text-align:center;font-size:12px;
                font-weight:600;line-height:0.46rem;
                &.pay{background:#007E36;color:#fff;font-size:16px;}

            };
        }
        .seletbox{
            width:100%;position:absolute;bottom:0.46rem;left:0;z-index:-100;
            .head{height:0.4rem;line-height:0.4rem;background:#F4F5F7;
                &>span:nth-of-type(1){float:left;padding-left:0.1rem;}
                &>span:nth-of-type(2){float:right;padding-right:0.1rem;color:#3AB5E1;}
            }
            .conts{max-height:2.25rem;overflow:hidden;background:#fff;
                .list{height:0.45rem;line-height:0.45rem;background:#fff;width:95%;margin:0 auto;border-bottom:1px solid #eee;
                    .listname{float:left;width:60%;}
                    .listprice{float:left;}
                    .addvue{float:right;width:25%;height:20px;position:relative;top:0.12rem;line-height:15px;}
                }
            }
        }
        .listmask{position:fixed;width:100%;height:100%;background:rgba(0,0,0,0.5);left:0;top:0;z-index:-101;}
    }

</style>


