<template>
    <div>
        <div class="sellbox" ref="cont">
            <div class="sellcont">
                <div class="name">
                    <div class="shopname">{{seller.name}}</div>
                    <div class="starshell">
                        <span><star :size="36" :score="seller.score"></star></span>
                        <span>（{{seller.ratingCount}}）</span><span>月售{{seller.sellCount}}单</span>
                    </div>
                    <div class="heart"></div>
                </div>
                <div class="des">
                    <div>
                        <div class="text">起送价</div>
                        <div class="money">{{seller.minPrice}}<sub>元</sub></div>
                    </div>
                    <div>
                        <div class="text">商家配送</div>
                        <div class="money">{{seller.deliveryPrice}}<sub>元</sub></div>
                    </div>
                    <div>
                        
                        <div class="text">平均配送时间</div>
                        <div class="money">{{seller.deliveryTime}}<sub>分钟</sub></div>
                    </div>

                </div>
                <div class="split"></div>
                <div class="notic">
                    <h2>公告与活动</h2>
                    <p>{{seller.bulletin}}</p>
                </div>
                <div class="split"></div>
                <div class="jing" >
                    <h2>商家实景</h2>
                    <div class="ulbox" ref="pic">
                        <ul class="imgs" ref="oul">
                            <li v-for=" imgpath in seller.pics"><img :src="imgpath" alt="" /></li>
                        </ul>
                    </div>
                    
                </div>
                <div class="split"></div>
                <div class="info">
                    <h2>商家信息</h2>
                    <ul>
                        <li v-for="list in seller.infos">{{list}}</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>
<script >
    import Star from './star/star.vue';   
    import Bscroll from 'better-scroll';
   
    export default{
        props:{
            seller:{
                type:Object,
                default:function(){
                    return {}
                }

            }
        },
        watch:{
            'seller':function(){  //检测seller的变化,有变化就执行函数
                this.initseller();
                this.initpic();
            }
        },
        mounted:function(){
            this.initseller();
            this.initpic();

        },
        data:function(){
            return {
                
            }
        },

        components:{
            star:Star
            
        },
        methods:{
            initseller:function(){
                var _this=this;
                _this.$nextTick( function () {
                    if(!_this.cont){
                            _this.cont=new Bscroll(_this.$refs.cont,{
                            click:true
                        });
                    }else{
                        _this.cont.refresh();
                    }
                     

                });
            },
            initpic:function(){
                //console.log(window.innerWidth);
                var _this=this;
                if(this.seller.pics){

                    //var liw=this.$refs.oul.getElementsByTagName('img')[0].offsetWidth;
                    var liw=120;
                    var len=this.seller.pics.length;
                    this.$refs.oul.style.width=(liw+6)*len+'px';  
                    _this.$nextTick( function () {
                        if(!_this.pic){
                            _this.pic=new Bscroll(_this.$refs.pic,{
                                scrollX:true,
                                eventPassthrough:'vertical'
                            });
                        }else{
                            _this.pic.refresh();
                        }
                         

                    });

                }
                
            }
        }
    }
</script>
<style lang="scss" scoped>
    .sellbox{
        position:absolute;top:1.55rem;bottom:0;width:100%;overflow:hidden;
        .name{padding:0.18rem;
            .shopname{font-size:14px;line-height:20px;}
            .starshell{padding-bottom:0.18rem;padding-top:0.05rem;border-bottom:1px solid #eee;
                &>span{display:inline-block;vertical-align:middle;}
            }
        };
        .des{padding:0.18rem 0;display:flex;
            &>div{flex:1;border-right:1px solid #eee;}
            &>div:nth-of-type(3){border:0;}
            .text{color:#999;text-align:center;margin-bottom:4px;}
            .money{font-size:24px;text-align:center;}
        };
        .split{height:0.1rem;background:#eee;border-top:1px solid #ddd;border-bottom:1px solid #ddd;}
        .notic{padding:0.18rem;
            p{color:#F01414;text-indent:24px;line-height:20px;margin-top:0.1rem;}
        }
        .jing{padding:0.18rem;
            .ulbox{overflow:hidden;
                .imgs{margin-top:0.1rem;height:0.9rem;white-space:nowrap;
                    font-size:0;
                    li{ margin-right:6px;display:inline-block;
                        &:last-child{margin:0;}
                        img{width:1.2rem;height:0.9rem;display:block;}
                    
                    }
                }
            }
            
        }
        .info{
            padding:0.18rem;
            h2{font-size:14px;}
            ul{margin-top:0.1rem;
                li{padding:0.1rem 0 0.1rem 0.12rem;;line-height:20px;border-top:1px solid #eee;}
            }
        }
        
    }
</style>


