<template>
    <div>
        <div class="wrap">
            <div class="menu" ref="menu">
                <ul>
                    <li class="itemlist" v-for="(item,index) in goods" :class="{active:currentIndex==index}" @click="sellectmenu(index,$event)">                     
                        <span class="text">
                            <span class="icon" :class="classarr[item.type]" v-show="item.type>0"></span>
                            {{item.name}}
                        </span>
                    </li>
                </ul>
            </div>
            <div class="goodsbox" ref="goodsbox">
               
                <ul>
                    <li class="goodslist" v-for="detail in goods">
                        <h2>{{detail.name}}</h2>
                        <ul>
                            <li class="detitem" v-for="item in detail.foods" @click.stop="showfood(item)">
                                <img :src="item.image" alt="" />
                                <div class="cont">
                                    <div class="title">{{item.name}}</div>
                                    <div class="des" v-if="item.description!=''">{{item.description}}</div>
                                    <div class="shou">
                                        月售{{item.sellCount}}份&nbsp;
                                        好评率{{item.rating | baifen}}
                                    </div>
                                    <div class="price">
                                        <span class="now">￥{{item.price}}</span>
                                        <span class="old" v-show="item.oldPrice">{{item.oldPrice}}</span>
                                    </div>
                                </div>
                                <div class="ctrl">
                                    <add :food="item" @cartAdd="listen"></add>
                                </div>
                            </li>
                        </ul>
                        
                    </li>
                </ul>
            </div>
            
            <shopcar :sellectfoods="selectfoods"
            :deliveryprice="seller.deliveryPrice" :minPrice="seller.minPrice" ref="shopcart"></shopcar>
            <food :food="clickfood" ref="foodvue"></food>
       </div>
    </div>
</template>
<script type="text/javascript">
    import Bscroll from 'better-scroll';
    import Shopcar from './shopcart/shopcart.vue';
    import add from './add.vue';
    import Food from './food.vue';
    export default{
        props:{
            seller:{
                type:Object
            } 
        },
        data:function(){
            return {
                goods:{},
                classarr:['jian','zhe','bao','piao','te'],
                listheight:[],
                scrollY:0,
                clickfood:{}
            }
        },
        computed:{

            currentIndex:function(){
            
                for(var i=0;i<this.listheight.length;i++){
                   
                    var nowheight=this.listheight[i];  
                    var nextheight=this.listheight[i+1];  
                    //console.log(this.scrollY+'...'+nowheight+'...'+nextheight);
                    if(!nextheight || (this.scrollY>=nowheight&&this.scrollY<nextheight) ){
                        return i;                       
                    }
                }
                return 0;
            },
            //计算传给购物车的数据
            selectfoods:function(){
                var foods=[];
                for(var i=0;i<this.goods.length;i++){
                    var good=this.goods[i];
                    //console.log(good.foods);
                    for(var a=0;a<good.foods.length;a++){
                        var food=good.foods[a];
                        if(food.count){
                            foods.push(food);
                        }
                        
                    }
                }
                return foods;
            }
        },
        mounted:function(){ 

            var _this=this;
            this.$ajax.get('/api/goods')
            .then(function (res) {
                
                if(res.data.errno==0){
                    var resp=res.data.data;
                    _this.goods=resp;
                    
                    _this.$nextTick( function () { //dom更新加载完再执行scroll函数              
                        _this.initScroll();
                        _this.calcheight();
                      
                    });
                }
            })
            .catch(function (error) {
                console.log(error);
            });            
        },
        methods:{
            calcheight:function(){
                /*1.计算每个区块的高度*/
                var height=0;
                var foodlist=this.$refs.goodsbox.getElementsByClassName('goodslist');
                this.listheight.push(height);
                for(var i=0;i<foodlist.length;i++){
                    height+=foodlist[i].clientHeight;
                    this.listheight.push(height);
                }
            },
            initScroll:function(){
                var _this=this;
                this.menuscroll=new Bscroll(this.$refs.menu,{
                    click:true
                });
                this.foodsscroll=new Bscroll(this.$refs.goodsbox,{
                    probeType :3,   //列表滚动时实时监听滚动的位置
                    click:true
                });
                this.foodsscroll.on('scroll',function(posy){ 
                    //实时获取滚动的大小，并赋值给data 
                    _this.scrollY=Math.abs(Math.round(posy.y));
                    //console.log(this.scrollY);
                });
            },
            sellectmenu:function(a,ev){
                if(!ev._constructed){  //better-scroll自定义派发的事件，原生浏览器是没有的
                    return;
                }
                //alert(a);
                var foodlist=this.$refs.goodsbox.getElementsByClassName('goodslist');
                var el=foodlist[a];
                this.foodsscroll.scrollToElement(el,300);
            },
            toshopcart:function(target){
                this.$refs.shopcart.childfn(target);  //使用该方法调用子组件的childfn 方法
            },
            listen:function(target){  //listen为子组件传递给父组件触发的函数
                this.toshopcart(target);
            },
            showfood:function(food){
                this.clickfood=food;
                this.$refs.foodvue.show();    //在父页面触发子页面的事件food.vue
            }
        },
        filters:{
            baifen:function(value){
                return value+'%';
            }
        },
        components:{
            shopcar:Shopcar,
            add:add,
            food:Food
        }
    }
</script>
<style lang="scss" scoped>
    @import "../../common/main.scss";
    .wrap{ 
        position:absolute;
        width:100%;
        top:1.55rem;left:0;bottom:0.46rem;
        overflow:hidden;      
        display:flex;
        .menu{
            width:0.8rem;
            flex:0 0 0.8rem;           
            background:#eee;
            .itemlist{                
                display:table;               
                width:0.7rem;
                height:0.6rem;
                padding-left:0.05rem;
                text-align:center;
                &.active{
                    color:#f60;font-size:14px;position:relative;z-index:1000;
                    .text{font-weight:600;border-bottom:1px #f60 solid;background:#fff;}
                }
                .text{
                    line-height:16px;
                    width:0.6rem;
                    display:table-cell;
                    vertical-align:middle;
                    border-bottom:1px solid #ccc;
                    .icon{
                        display:inline-block;
                        width:0.12rem;
                        height:0.12rem;
                        @extend %background;
                        &.jian{
                            background-image:url(decrease_3@2x.png);
                        };
                        &.bao{
                             background-image:url(guarantee_1@2x.png);
                        };
                        &.piao{
                             background-image:url(invoice_1@2x.png);
                        };
                        &.te{
                             background-image:url(special_1@2x.png);
                        };
                        &.zhe{
                             background-image:url(discount_1@2x.png);
                        };

                    }
                }
            }
        }
        .goodsbox{
            flex:1;
            .goodslist{
                h2{background:#eee;line-height:0.3rem;font-size:16px;border-left:2px solid #ccc;}
                .detitem{
                    padding:0.05rem;position:relative;
                    @include clearfloat;
                    img{
                        width:0.6rem;
                        height:0.6rem;
                        float:left;
                    }
                    .cont{
                        float:left;
                        width:55%;
                        padding-left:0.05rem;
                        .title{
                            line-height:0.2rem;
                            font-size:14px;font-weight:600;
                        }
                        .des{line-height:0.2rem;}
                        .shou{line-height:0.2rem;}
                        .now{line-height:0.2rem;font-weight:600;color:red;font-size:14px;}
                        .old{text-decoration:line-through;}
                    }
                    .ctrl{
                        float:left;position:absolute;width:21%;bottom:0.1rem;;right:0;
                        
                    }
                }
            }
           
        }
    }

</style>


