<template>
    <div>
        <div class="rating" ref="cont">
            <div class="ratingcont">
                <div class="score">
                    <div class="scoreleft">
                        <div class="zh">{{seller.score}}</div>
                        <div class="zhtext">综合评分</div>
                        <div class="bj">高于周边商家{{seller.rankRate}}</div>
                    </div>
                    <div class="scoreright">
                        <div class="fwtd">服务态度<span><star :size="36" :score="seller.serviceScore"></star></span>{{seller.serviceScore}}</div>
                        <div class="sppf">商品评分<span><star :size="36" :score="seller.foodScore"></star></span>{{seller.foodScore}}</div>
                        <div class="cometime">送达时间&nbsp;&nbsp;{{seller.deliveryTime}}分钟</div>
                    </div>
                    
                </div>
                <div class="split"></div>
                <div class="ratingvuebox">
                    <rating  :rating="ratings" :checkcount="checkcount" :ratingtype="ratingtype" @selecttype="gettype" @checktype="getif"></rating>
                </div>
                <div class="box">
                    <ul>
                        <li v-for="item in ratings" v-show="needshow(item.rateType,item.text)">
                            <div class="top">
                                <img :src="item.avatar" alt="" />
                                <div class="userstar">
                                    <div class="user">{{item.username}}</div>
                                    <div class="star">
                                        <span class="stars"><star :size="24" :score="item.score"></star></span>
                                        <span v-show="item.deliveryTime ">{{item.deliveryTime}}分钟送达</span>
                                    </div>
                                </div>
                                <div class="date">{{item.rateTime |time}}</div>
                            </div>
                            <div class="text">
                                {{item.text}}
                            </div>
                            <div class="recommend" v-show="item.recommend!=''">
                                <span class="icon"></span><span v-for="(rec,index) in item.recommend">{{rec}}</span>
                            </div>
                        </li>
                       
                    </ul>
                       
                </div>
            </div>
        </div>
    </div>
</template>
<script >
    import Star from './star/star.vue';
    import Rating from './goods/ratingselect.vue';
    import Bscroll from 'better-scroll';
    import {fometdate} from '../js/date.js';
    var all=2;
    var yes=0;
    var no=1;
    export default{
        props:{
            seller:{
                type:Object,
                default:function(){
                    return {}
                }

            }
        },
        mounted:function(){
            var _this=this;
            this.$ajax.get('/api/ratings')
                .then(function (res) {
                  
                  if(res.data.errno==0){
                    var resp=res.data.data;
                    _this.ratings=resp;

                    _this.$nextTick( function () {
                        if(!_this.cont){
                             _this.cont=new Bscroll(_this.$refs.cont,{
                                click:true
                            });
                        }else{
                            _this.cont.refresh();
                        }
                        

                    });
                   
                  }

                })
                .catch(function (error) {
                  console.log(error);
                });
        },
        data:function(){
            return {
                ratings:[],                
                checkcount:false,
                ratingtype:all
            }
        },
        components:{
            star:Star,
            rating:Rating
        },
        methods:{
            needshow:function(type,text){
                if(this.checkcount&&!text){   //如果需要查看仅有内容的选项但评论为空，返回false
                    return false;
                };
                if(this.ratingtype===all){   //如果点击状态为 全部 ，就显示全部,默认传递的为all
                    return true;
                }else{
                    return type==this.ratingtype;    //否则就判断点击状态跟评论状态是否一样
                }
            },
            gettype:function(type){
                this.ratingtype=type;
                this.$nextTick( function () {
                    this.cont.refresh();
                });
            },
            getif:function(is){
                this.checkcount=is;
                this.$nextTick( function () {
                    this.cont.refresh();
                });
            }
        },
        filters:{
            time:function(rateTime){     //时间过滤
                return fometdate(rateTime);
            }
        }


    }
</script>
<style lang="scss" scoped>
    .rating{
        position:absolute;top:1.55rem;left:0;bottom:0;width:100%;overflow:hidden;
        .score{
            display:flex;padding:0.18rem 0;
            .scoreleft{flex:0 0 1.35rem;width:1.35rem;border-right:1px solid #eee;
                .zh{color:#FF9900;font-size:24px;line-height:28px;text-align:center;}
                .zhtext{color:#333;font-size:14px;line-height:20px;text-align:center;}
                .bj{color:#ccc;font-size:12px;line-height:20px;text-align:center;line-height:14px;}
            }
            .scoreright{flex:1;
                &>div{line-height:18px;padding-left:0.05rem;
                    span{display:inline-block;vertical-align:top;position:relative;top:3px;padding-left:0.05rem;}
                }
            }            
        };
        .split{height:0.1rem;background:#eee;border-top:1px solid #ddd;border-bottom:1px solid #ddd;}
        .box{
            li{padding:0.05rem;
                &:nth-of-type(1){
                    .top{
                        border:0;
                    }
                    
                };
                .top{
                    position:relative;display:flex;padding:0.1rem 0;border-top:1px solid #eee;

                    img{width:0.28rem;height:0.28rem;border-radius:50%;vertical-align:middle;flex:0 0 0.28rem;}
                    .userstar{display:inline-block;vertical-align:middle;flex:1;padding-left:0.1rem;
                        .star{
                            .stars{display:inline-block;}
                            span:nth-of-type(2){color:#999;}
                        }
                    }
                    .date{display:inline-block;vertical-align:top;position:absolute;top:0.05rem;right:0;color:#999;}
                };
                .text{
                    line-height:20px;font-size:14px;padding-left:0.38rem;color:#000;
                };
                .recommend{padding:0.1rem 0 0 0.38rem;color:#999;
                    &>span{display:inline-block;margin-right:0.1rem;}
                    .icon{width:12px;height:12px;background:#0082D7;border-radius:50%;vertical-align:top;}
                }

            }
        }
    }
</style>


