<template>
  <div id="app">
    <heade :seller="seller"></heade>
    <div class="tab">
      <ul>
        <li><router-link to="/goods">商品</router-link></li>
        <li><router-link to="/ratings">评论</router-link></li>
        <li><router-link to="/seller">商家</router-link></li>
      </ul>
      <keep-alive><router-view :seller="seller" ></router-view></keep-alive>
    </div>
   
  </div>
</template>

<script>
import Header from './components/header.vue';


export default {
  name: 'app',
  data:function(){
    return {
        seller:{},
        msg:'123'
    }
  },
  mounted:function(){
    //console.log(axios);
    this.get();
  },
  methods:{
    get:function(){
      var _this=this;
      this.$ajax.get('/api/seller')
        .then(function (res) {
          
          if(res.data.errno==0){
            var resp=res.data.data;
            _this.seller=resp;
            
          }

        })
        .catch(function (error) {
          console.log(error);
        });
    }     
  },
  components:{
    heade:Header
    
  }
}
</script>

<style lang="scss" scoped>
@import "./common/main.scss";
    
  //li{background:red;}  
  

.tab {
      height:auto;
      ul{
        display:flex;
        @include border1px(#ccc);
        li{
          flex:1;
          
          height:0.4rem;
          line-height:0.4rem;
          text-align:center;
          &>a{   //&指代父元素 li
            display:block;
            font-size:14px;
            color:rgb(77,85,93);
            &.router-link-active{
              color:rgb(240,20,20);
            }
          }
        }
      }
    }

</style>




