<template>
    <div >
        <transition enter-active-class="slideInLeft"
        leave-active-class="slideOutRight">
        <div class="foodcont animated" v-show="flag" ref="scroll">
            <div>
                <div class="imgcont">
                    <img :src="food.image" alt=""/>
                    <div class="close"  @click="hide">&times;</div>
                </div>
                <div class="des">
                    <div class="des-name">{{food.name}}</div>
                    <div class="des-des" v-if="food.description!=''">{{food.description}}</div>
                    <div class="des-shou">
                        月售{{food.sellCount}}份&nbsp;
                        好评率{{food.rating}}%
                    </div>
                    <div class="des-price">
                        <span class="now">￥{{food.price}}</span>
                        <span class="old" v-show="food.oldPrice">{{food.oldPrice}}</span>
                    </div>
                    <div class="ctr">
                        <div class="add-box">
                            <add :food="food"></add>
                        </div>
                        <div class="addfirst" v-if="!food.count||food.count===0" @click="addfirst">加入购物车</div>
                    </div>
                </div>
                <div class="split" v-if="food.info"></div>
                <div class="info" v-if="food.info">
                    <h2>商品信息</h2>
                    <p>{{food.info}}</p>
                </div>
                
                <div class="split" ></div>
                <div class="rating" >
                    <h2>商品评价</h2>
                   
                    <rating :des="des" :rating="food.ratings" :checkcount="checkcount" :ratingtype="ratingtype" @selecttype="gettype" @checktype="getif"></rating>
                </div>
                <div class="ratingbox">
                    <ul v-show="food.ratings&&food.ratings.length">
                        <li v-for="item in food.ratings" v-show="needshow(item.rateType,item.text)">
                            <div class="dateUser">
                                <span class="date">{{item.rateTime | time}}</span>
                                <span class="user">{{item.username}}
                                    <img :src="item.avatar" alt="" />
                                </span>
                            </div>
                            <div class="text" v-show="item.text"><span class="icon " :class="{'icono-meh':item.rateType==0,'icono-frown':item.rateType==1}"></span>{{item.text}}</div>
                            <div class="text" v-show="!item.text"><span class="icon" :class="{'icono-meh':item.rateType==0,'icono-frown':item.rateType==1}"></span>评论为空,思密达</div>
                        </li>
                    </ul>
                    <div v-show="!food.ratings || !food.ratings.length">暂无评论！</div>
                </div>
            </div>
        </div>
        </transition>
        
    </div>
</template>
<script >
    import add from './add.vue';
    import Vue from 'vue';
    import Bscroll from 'better-scroll';
    import rating from './ratingselect.vue';

    var all=2;
    var yes=0;
    var no=1;

    export default{

        props:{
            food:{
                type:Object
            } 
        },
        data:function(){
            return {
                flag:false,
                des:{
                    all:'全部',
                    yes:'满意',
                    no:'吐槽'
                },
                checkcount:false,
                ratingtype:all

            }
        },
        mounted:function(){
           //console.log(this.food);
        },
        methods:{
            show:function(){
                

                this.flag=true;
                this.checkcount=false;
                this.ratingtype=all;
                var _this=this;
                this.$nextTick( function () { //dom更新加载完再执行scroll函数   
                    if(!_this.conts) {
                        _this.conts=new Bscroll(_this.$refs.scroll,{
                            click:true
                        });
                    }else{   //如果已经有实例化的话，直接执行refresh方法
                        _this.conts.refresh();
                    }         
                    
                  
                });
            },
            hide:function(){
                this.flag=false;
                this.checkcount=false;

            },
            addfirst:function(){

                if(!this.food.count){
                    Vue.set(this.food,'count',1);
                }
            },
            needshow:function(type,text){
                if(this.checkcount&&!text){   //如果需要查看仅有内容的选项但评论为空，返回false
                    return false;
                };
                if(this.ratingtype===all){   //如果点击状态为 全部 ，就显示全部,默认传递的为all
                    return true;
                }else{
                    return type==this.ratingtype;    //否则就判断点击状态跟评论状态是否一样
                }
            },
            gettype:function(type){
                this.ratingtype=type;
                this.$nextTick( function () {
                    this.conts.refresh();
                });
            },
            getif:function(is){               
                this.checkcount=is;
                this.$nextTick( function () {
                    this.conts.refresh();
                });
            }
        },
        components:{
            add:add,
            rating:rating
        },
        filters:{
            time:function(rateTime){     //时间过滤
                var time=new Date(rateTime);
                var year=time.getFullYear(); 
                var month=time.getMonth()+1; 
                var date=time.getDate(); 
                var hour=time.getHours(); 
                var minute=time.getMinutes(); 
                var second=time.getSeconds(); 
                return year+"-"+month+"-"+date+" "+hour+":"+minute+":"+second;    
            }
        }
        
    }

</script>
<style lang="scss" scoped>
    %pad{padding:0.05rem}
   
    .foodcont{position:fixed;width:100%;left:0;top:0;bottom:0.46rem;background:#fff;z-index:100;
        .imgcont{width:100%;padding-top:100%;position:relative;
            img{position:absolute;top:0;left:0;width:100%;height:100%;}
            .close{width:30px;height:30px;position:absolute;right:10px;top:10px;font-size:30px;color:#fff;}
        };
        .des{
            padding:0.05rem;position:relative;
            .des-name{height:0.2rem;line-height:0.2rem;font-size:14px;font-weight:600;}
            .des-des{line-height:0.2rem;color:#999;}
            .des-shou{line-height:0.2rem;}
            .des-price{
                line-height:20px;
                .now{color:red;font-size:14px;}
                .old{text-decoration:line-through;}
            }
            .ctr{position:absolute;bottom:0.1rem;right:0.1rem;width:0.75rem;z-index:5;height:0.2rem;
                .add-box{position:absolute;right:0;top:0;width:0.55rem;z-index:10;}
                .addfirst{width:100%;background:#57BFFA;height:0.2rem;border-radius:20px;text-align:center;line-height:0.2rem;color:#fff;position:absolute;bottom:0;right:0;z-index:11;}
            };
            
        };
        .split{height:0.1rem;background:#eee;border-top:1px solid #ddd;border-bottom:1px solid #ddd;}
        .info{
            @extend %pad;
            h2{line-height:20px;font-size:14px;}
            p{color:#999;text-indent:24px;line-height:16px;}
        };
        .rating{
            @extend %pad;
            h2{line-height:20px;font-size:14px;}

        };
        .ratingbox{
            @extend %pad;
            li{
                .dateUser{height:20px;line-height:20px;
                    &>span{color:#999;}
                    .date{float:left;}
                    .user{
                        float:right;
                        img{width:12px;height:12px;vertical-align:middle;}
                    }
                };
                .text{
                    font-size:14px;text-indent:20px;line-height:30px;padding-bottom:0.08rem;border-bottom:1px solid #eee;
                    .icon{display:inline-block;width:30px;height:30px;border-radius:50%;background:#0082D7;color:#fff;
                        
                    }
                }
            }

        }
    }
</style>


