<template>
    <div>
       <div class="ratingvue">
           <span class="all" :class="{'active':ratingtype===2}" @click="select(2,$event)" >{{des.all}} <span class="count">{{rating.length}}</span></span>
           <span class="yes" :class="{'active':ratingtype===0}" @click="select(0,$event)" >{{des.yes}} <span class="count">{{yeslength.length}}</span></span>
           <span class="no" :class="{'active':ratingtype===1}" @click="select(1,$event) ">{{des.no}} <span class="count">{{nolength.length}}</span></span>
       </div>
       <div class="switch">
           
           <span class="check1 icono-checkCircle" @click="checked" :class="{on:checkcount==true}"></span>
           <span :class="{on:checkcount==true}" class="textcont">只看有内容的评价</span>
       </div>
       
        
    </div>
</template>

<script type="text/javascript">
    import Vue from 'vue';

    var all=2;
    var yes=0;
    var no=1;
    export default{
        props:{
            des:{
                type:Object,
                default:function(){
                    return {
                        all:'全部',
                        yes:'满意',
                        no:'不满意'
                    }

                }
            },
            ratingtype:{
                type:Number,
                default:all
            },
            rating:{
                type:Array,
                default:function(){
                    return [ ];
                }
            },
            checkcount:{   //是否只读有内容的
                type:Boolean,
                default:function(){
                    return false;
                }
            }
        },
        data:function(){
            return {
                ifcheck:false,
                ratingtype1:all
            }
        },
        computed:{
            yeslength:function(){
                return this.rating.filter(function(rating){
                    return rating.rateType===yes;
                })
            },
            nolength:function(){
                return this.rating.filter(function(rating){
                    return rating.rateType===no;
                })
            }
        },
        mounted:function(){
           
        },
        methods:{
            select:function(type,ev){

                if(!ev._constructed){  
                    return;
                }   

                this.$emit('selecttype',type);
            },
            checked:function(){
                this.ifcheck=!this.ifcheck;
                //this.checkcount=!this.checkcount;
                this.$emit('checktype',this.ifcheck);
            }
        }
            
    }
</script>

<style lang="scss" scoped>
   .ratingvue{
        padding:0.1rem;
        font-size:0;
        &>span{display:inline-block;padding: 8px 10px;font-size:12px;margin-left:0.1rem;
            &.active{color:#fff;}
            .count{font-size:8px;margin-left:2px;}
        }
        .all{background:#C5E5F6;
            &.active{
                background:#0082D7;color:#fff;
            }
        }
        .yes{background:#C5E5F6;
            &.active{
                background:#0082D7;color:#fff;
            }
        }
        .no{
            background:#eee;
            &.active{
                background:#999;color:#fff;
            }
        }
   };
   .switch{
        padding:0.1rem;border-top:1px solid #eee;border-bottom:1px solid #eee;
        .check1{vertical-align:middle;display:inline-block;width:30px;height:30px;border-radius:50%;background:#eee;color:#fff;
            &.on{background:#0082D7;}

        }
        .textcont{
            color:#999;
            &.on{color:#333;}
        }
   }
</style>


